import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Message, ConfirmationService, MenuItem } from 'primeng/primeng';
import { Commfunc } from '../../../../@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../@uisftech/common/constant/config';
import { SerialNumberQueryBean, SerialNumberErrorInfoBean } from '../bean/serial-number.bean';
import { SerialNumberHttpService } from '../http/serial-number.http.service';

@Component({
    selector: 'app-serial-number-query',
    templateUrl: './serial-number-query.html',
    styleUrls: ['./serial-number-query.scss'],
    providers: [ConfirmationService, SerialNumberHttpService]
})

export class SerialNumberQueryComponent implements OnInit {

    // flowNoExampleList: any;//流程实例
    flowNoTaskList: any = [];//审批轨迹
    errMessageList: any;//错误信息
    flowNo: string;//当前选中节点的流水号

    arr1: any;//
    arr2: any;//

    tabMesg: string = tableMessage;//表格没有数据时的展示内容
    msgs: Message[] = [];//信息提示框

    first: number = 0;//首页(分页)

    serialNumberQueryBean: SerialNumberQueryBean = new SerialNumberQueryBean();
    serialNumberErrorInfoBean: SerialNumberErrorInfoBean = new SerialNumberErrorInfoBean();

    code: any;//码值

    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public serialNumberHttpService: SerialNumberHttpService,
        public routerInfo: ActivatedRoute,
    ) {
        this.codeValues();
    }

    ngOnInit() {

        if (this.routerInfo.snapshot.queryParams['mainFlowNo'] && this.routerInfo.snapshot.queryParams['mainFlowNo'] != '') {
            this.serialNumberQueryBean.mainFlowNo = this.routerInfo.snapshot.queryParams['mainFlowNo'];
        }

        this.bpmQueryFlowNoTaskList();//
        
        this.preSet();
        setTimeout(() => {//
            this.errMessageList = [];
        });  
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    //初始化
    preSet() {
        // this.arr1 = [{//流程实例表头
        //     ch: '流程名称',
        //     label: 'bigClassName'
        // },{
        //     ch: '系统编码',
        //     label: 'systemCode'
        // }];

        this.arr2 = [{//错误信息表头
            ch: '系统编号',
            label: 'targetId'
        }, {
            ch: '流水号',
            label: 'globalSeqNo'
        }, {
            ch: '错误码',
            label: 'errCode'
        }, {
            ch: '错误信息',
            label: 'errMsg'
        }];
    }

    //节点-点击
    doClick(item) {
        if (item.isException == '0') {
            return false
        }

        this.flowNo = this.serialNumberQueryBean.mainFlowNo;//当前流水号
        this.queryErrMessage(this.flowNo);
    }

    //查看审批轨迹
    // showDetail(col,i){
    //     console.log(col);
    // }

    //流水号查询审批轨迹
    bpmQueryFlowNoTaskList() {
        // debugger
        // 校验
        if (this.serialNumberQueryBean.mainFlowNo) {
            if (this.serialNumberQueryBean.mainFlowNo.length < 23) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '流水号应是不小于23位的数字' }];
                return false;
            }
        } else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '流水号不能为空' }];
            return false;
        }

        this.serialNumberHttpService.bpmQueryFlowNoTaskList(this.serialNumberQueryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // console.log(data);
                this.flowNoTaskList = [];
                if (data.pageData.result && data.pageData.result.length != 0) {
                    data.pageData.result.forEach((element, i) => {
                        let obj = {
                            taskName: '',
                            doTaskActorName: '',
                            isException: ''
                        };
                        obj.taskName = element.taskName;//流程名称
                        obj.doTaskActorName = element.doTaskActorName;//任务处理人
                        obj.isException = element.isExcepiton;//状态
                        this.flowNoTaskList[i] = obj;
                    });
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        })
    }

    //节点错误信息
    queryErrMessage(flowNo) {
        let param = {
            globalSeqNo: flowNo,
            pageNum: this.serialNumberErrorInfoBean.pageNum,
            pageSize: this.serialNumberErrorInfoBean.pageSize
        }
        this.serialNumberHttpService.queryErrMessage(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // console.log(data);
                this.serialNumberErrorInfoBean.total = data.total;
                this.errMessageList = data.valueList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
        })
    }

    //分页
    paginate(event) {
        this.serialNumberErrorInfoBean.pageSize = Number(event.rows);//每页显示的条数
        let currentPage = event.page + 1;//当前页
        this.serialNumberErrorInfoBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.serialNumberErrorInfoBean.pageSize;
        this.queryErrMessage(this.flowNo);//调用查询的方法
    }
}