import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';

import { SerialNumberComponent } from './serial-number.componen';
import { SerialNumberQueryComponent } from './serial-number-query/serial-number-query';

export const routes: Routes = [
    {
        path: '',
        component: SerialNumberComponent,
        children: [
            {
                path: 'serial-number-query',
                component: SerialNumberQueryComponent
            },
        ]
    },
];

export const SerialNumberRouting: ModuleWithProviders = RouterModule.forChild(routes);