import { inject } from '@angular/core/testing';
import { Injectable, NgModule } from '@angular/core';
import { HttpService } from 'app/@uisftech/common/service/http.service';
import { TZB_HTTP_URL,TZB_HTTP_URL1 } from 'app/@uisftech/common/service/http.constant';
import { API } from './serial-number.http.constant'

@Injectable()
export class SerialNumberHttpService {
    
    constructor(public httpService: HttpService) {

    }

    //流水号审批记录查询接口
    bpmQueryFlowNoTaskList(params) {
        params = this.httpService.handleParams(params)
        return this.httpService.post(TZB_HTTP_URL + API.bpmQueryFlowNoTaskList, params);
    }
    
    //节点错误信息查询接口
    queryErrMessage(params) {
        params = this.httpService.handleParams(params)
        return this.httpService.post(TZB_HTTP_URL1 + API.queryErrMessage, params);
    }
}